.contact-btn {
    position: absolute;
    top: 13px;
    right: 30px;
    text-align: right;
    padding: 10px 0;
    font-size: 15px;
    font-weight: 700;
    cursor: pointer;
    z-index: 999;
    transition: all 0.4s ease;
    opacity: 1;
}

.close-btn {
    transition: all 0.4s ease;
    opacity: 1;
    color: #000;
}

.contact-details {
    position: absolute;
    top: -100%;
    transition: all 0.4s ease;
    opacity: 1;
    color: #000;
    font-family: 'Raleway', sans-serif;
    font-weight: 700;
    font-size: 40px;
    letter-spacing: 3px;
    padding-left: 60px;
    height: 40vh;
    width: 100%;
}

.contact-details.active {
    background:#fdd85f;
    top: 0;
    transition: all 0.4s ease;
    opacity: 1;
    z-index: 998;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

a.link-no-style {
    color: inherit;
    text-decoration: none;
}



@media (max-width: 1024px) {
    .contact-details {
        font-size: 35px;
    }

}


@media (max-width: 768px) {
    .contact-details {
        font-size: 28px;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        align-content: center;
        justify-content: center;
        padding: 0;
    }

    .active-mb {
        position: fixed;
    }


}


@media (max-width: 414px) {
    .contact-details {
        font-size: 24px;
        letter-spacing: 1px;
    }

}

@media (max-width: 320px) {
    .contact-details {
        font-size: 22px;
    }

}

